}
.formulario .campo {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 6rem;
  font-size: 1rem;
}
@media (min-width: 768px) {
  .formulario .campo label {
    font-size: 1.4rem;
    flex: 0 0 10rem;
  }
}
