.papelera {
  display: none;
  position: absolute;
  bottom: 13%;
  right: 42%;
  opacity: 0.8;
  background-color: rgba(225, 131, 131, 0.879);
  max-width: 70px;
  min-height: 50px;
  border-radius: 50%;
  margin: 0 auto;
  transition: transform 0.3s;
}
@media (min-width: 768px) {
  .papelera {
    right: 36%;
  }
}
@media (min-width: 1024px) {
  .papelera {
    right: 40%;
  }
}
.papelera svg {
  padding: 1rem 0.5rem;
  width: 60px;
  height: 100%;
  display: block;
  object-fit: cover;
}
.papelera:hover {
  transform: scale(1.065);
}

